#sequenceline {
  display: flex;
  padding: 8px;
  overflow-x: auto;
  overflow-y: hidden;
  flex: 1 1;
}
.add-btn {
  height: 190px;
  width: 10px;
  float: right;
  right: -10px;
  display: block;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  bottom: 190px;
  z-index: 999;
}
.add-btn .add-next-insight {
  width: 25px;
  height: 25px;
  display: none;
}
.final-add-btn {
  height: 190px;
  width: 10px;
  margin-top: 8px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  z-index: 999;
}
.final-add-btn .add-next-insight {
  width: 25px;
  height: 25px;
  display: none;
}
.add-btn:hover {
  background: rgba(255, 186, 49, 0.446);
  box-shadow: -5px -1px 20px 5px rgba(255, 190, 49, 0.729);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.add-btn:hover .add-next-insight {
  width: 20px;
  height: 20px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/add_fact.png") no-repeat center;
  background-size: 16px 16px;
  background-color: white;
  border-radius: 20px;
  margin-bottom: 10px;
  border: #484F5D 1px solid;
  display: block;
  cursor: pointer;
  margin-right: 8px;
}
.add-btn:hover .add-next-insight:hover {
  border: #F59B48 2px solid;
}
.final-add-btn:hover {
  background: rgba(255, 186, 49, 0.446);
  box-shadow: -5px -1px 20px 5px rgba(255, 190, 49, 0.729);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.final-add-btn:hover .add-next-insight {
  width: 20px;
  height: 20px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/add_fact.png") no-repeat center;
  background-size: 16px 16px;
  background-color: white;
  border-radius: 20px;
  margin-bottom: 10px;
  border: #484F5D 1px solid;
  display: block;
  cursor: pointer;
  margin-right: 8px;
}
::-webkit-scrollbar {
  width: 2px;
  height: 4px;
  display: block;
}
/**/
::-webkit-scrollbar-track {
  border-radius: 10px;
  border: #ffffff;
  background: #ffffff;
}
/* */
::-webkit-scrollbar-thumb {
  border-radius: 14px;
  background-clip: padding-box;
  background-color: #C4C4C4;
}
