.timeline-hightlight {
  color: #F6C02A;
}
.insight-card {
  width: 170px;
  height: 190px;
  padding: 6px 5px 0px 5px;
  margin-left: 3px;
  margin-top: 5px;
  margin-right: 3px;
  background: #F4F4F4;
}
.insight-card .close-icon {
  color: #484F5D;
  padding: 6;
  position: absolute;
  z-index: 2;
  top: 3px;
  right: 5px;
  display: inline;
}
.insight-card .close-icon:hover {
  color: #F28B30;
}
.insight-card .insight-preview {
  width: 160px;
  height: 110px;
  margin-bottom: 5px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.insight-card .script-preview {
  width: 100%;
  padding-left: 5px 2px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  word-break: break-all;
  -webkit-line-clamp: 4;
  overflow: hidden;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 10px;
  color: #000000;
  text-align: justify;
  line-height: 1.1;
  height: 100px;
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
