.time-analysis-right-wrapper {
  height: 100%;
  padding: 0px 8px 0px 8px;
  background: #FFFFFF;
  box-shadow: 2px 3px 10px 0 rgba(165, 165, 165, 0.5);
}
.time-analysis-right-wrapper .init-edit-pannel {
  height: calc(100% - 45px) !important;
  overflow-y: auto;
  overflow-x: hidden;
  padding-right: 3px;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse {
  background-color: #fff !important;
  border: 0 !important;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse > .ant-collapse-item:last-child,
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse > .ant-collapse-item:last-child > .ant-collapse-header {
  border-radius: 0 0 4px 4px;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  font-size: 14px !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  font-weight: 400;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse-content {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  background-color: #fff;
  border: 1px solid #979797;
  height: 40%;
  width: 286px;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse-content > .ant-collapse-content-box {
  padding: 5px !important;
  width: 285px;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-select {
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5;
  -webkit-font-feature-settings: "tnum", "tnum", "tnum";
          font-feature-settings: "tnum", "tnum", "tnum";
  position: relative;
  display: inline-block;
  outline: 0;
}
.time-analysis-right-wrapper .init-edit-pannel .ant-collapse-content {
  color: rgba(0, 0, 0, 0.85);
}
.time-analysis-right-wrapper .init-edit-pannel .ant-select:not(.ant-select-customize-input) .ant-select-selector {
  border-radius: 4px;
}
.time-analysis-right-wrapper .start-back-btn .edit-panel-btn {
  width: 290px;
  display: inline;
  margin-top: 5px;
  border: none;
  float: left;
}
.time-analysis-right-wrapper .start-back-btn .edit-panel-btn .ant-col:hover {
  background: #FCBE31;
  box-shadow: 0 1px 3px 0 #FCBE31;
}
.time-analysis-right-wrapper .start-back-btn .edit-panel-btn .ant-col {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 36px;
  background: #DBDBDB;
  border-radius: 5px;
  cursor: pointer;
}
.time-analysis-right-wrapper .start-back-btn .edit-panel-btn .ant-col div {
  width: 16px;
  height: 16px;
  margin-right: 0px;
}
.time-analysis-right-wrapper .start-back-btn .edit-panel-btn .ant-col p {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 0px;
  font-size: 14px;
}
@font-face {
  font-family: "NotoSansHans-Thin-Windows";
  src: url(/static/media/NotoSansHans-Thin-Windows.0f395cd2.ttf), url(/static/media/NotoSansHans-Thin-Windows.ed2260a2.eot);
}
.ant-tabs {
  height: 100%;
}
.ant-tabs .ant-tabs-ink-bar {
  opacity: 0;
}
.ant-tabs .ant-tabs-tab-btn {
  left: 10px;
}
.ant-tabs-content {
  height: 100%;
}
.ant-tabs-tabpane ::-webkit-scrollbar {
  width: 4px;
  height: 2px;
}
/**/
.ant-tabs-tabpane ::-webkit-scrollbar-track {
  border-radius: 14px;
  border: #ffffff;
  background: #ffffff;
}
/* */
.ant-tabs-tabpane ::-webkit-scrollbar-thumb {
  border-radius: 14px;
  background-color: #dfdfdf;
}
