.ant-modal-mask {
  background-color: rgba(0, 0, 0, 0.02);
}
.pop-spin {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 140px;
}
.pop-insights-card .ant-modal-content {
  width: 480px;
  border-radius: 4px;
}
.pop-insights-card .ant-modal-header {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  height: 10px;
  border-radius: 4px;
  border-bottom: none;
}
.pop-insights-card .ant-modal-title {
  color: white;
}
.pop-insights-card .ant-modal-body {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  padding-top: 0;
  padding-right: 0;
  height: 300px;
}
.pop-insights-card .ant-tabs-nav-wrap {
  padding-left: 4px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.pop-insights-card .ant-modal-footer {
  text-align: center;
}
.pop-insights-card .ant-tabs-tab {
  padding: 0 10px 0 10px;
  border-right: 2px solid #EAEAEA;
}
.pop-insights-card .ant-tabs-tab .ant-tabs-tab-btn {
  font-size: 18px;
  font-weight: 600;
  color: #979797;
}
.pop-insights-card .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #f28b30;
}
.pop-insights-card .ant-tabs-tab + .ant-tabs-tab {
  margin: 0;
}
.pop-insights-card .vis {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pop-insights-card .ant-tabs {
  width: 100%;
}
.pop-insights-card .ant-tabs .ant-tabs-nav {
  margin-right: 20px;
}
.pop-card-footer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.pop-card-footer .card-footer-btn {
  cursor: pointer;
  width: 203px;
  height: 32px;
  background-color: #f28b30;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
}
.pop-card-footer .card-footer-btn .footer-btn-icon {
  line-height: 32px;
  width: 32px;
  height: 32px;
  font-size: 20px;
  color: white;
}
.pop-card-footer .card-footer-btn .footer-btn-text {
  padding-left: 5px;
  line-height: 32px;
  height: 32px;
  color: white;
  font-size: 14px;
}
