.editor-box {
  position: absolute;
  left: calc(100% - 398px);
}
.editor-box a {
  color: rgba(0, 0, 0, 0.85);
}
.editor-box .export-box-operator-wapper {
  width: 460px;
}
.editor-box .export-box-operator-wapper .export-box-operator {
  display: flex;
  width: 80px;
  box-shadow: -2px 0 10px 0 rgba(207, 207, 207, 0.5);
  background-color: white;
  justify-content: center;
  border-radius: 4px;
}
.editor-box .export-box-operator-wapper .export-box-operator .export-btn-div {
  font-size: 13px;
  line-height: 30px;
  text-align: center;
  display: inline-block;
}
.editor-box .export-box-operator-wapper .export-box-operator .export-btn-div .export-btn {
  width: 30px;
  height: 30px;
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 18px 18px;
  float: left;
  padding-left: 10px;
}
.editor-box .export-box-operator-wapper .export-box-operator:hover {
  cursor: pointer;
}
.editor-box .export-box-operator-wapper .export-box-operator:hover a {
  transition: color 0s;
  color: #ed9c04;
}
.editor-box .export-box-operator-wapper .export-box-operator:hover .export-btn {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 18px 18px;
  float: left;
}
