.file-information {
  margin-left: 6px;
  float: left;
}
.file-information .time-series-title-box {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 26px;
  color: #F28B30;
  height: 33px;
  line-height: 33px;
  margin-bottom: 1px !important;
  display: flex;
  flex-direction: row;
  align-items: center;
  word-break: keep-all;
}
.file-information .time-series-title {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 26px;
  height: 28px;
  line-height: 28px;
  color: #1A0000;
  margin-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.file-information .time-scope-box {
  display: flex;
  flex-direction: row;
  line-height: 14px;
}
.file-information .time-scope-box .time-scope {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #6D757A;
  margin-right: 15px;
  line-height: 21px;
  height: 21px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-information .time-scope-box .scope-title {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #6D757A;
  line-height: 21px;
  height: 21px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.file-information .time-scope-box .scope-value {
  margin-left: 2px;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: black;
  line-height: 21px;
  height: 21px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
