.VisOptionViewDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  z-index: 99;
}
.VisOptionViewDiv .optionsDiv {
  width: 140px;
  height: 20px;
  border: none;
  /* Select Options ** the end**/
}
.VisOptionViewDiv .optionsDiv .ant-select-selector {
  width: 140px;
  height: 20px;
  background: #F4F4F4;
  border-radius: 14.5px;
}
.VisOptionViewDiv .optionsDiv .ant-select-selector .ant-select-selection-search {
  height: 20px;
  margin-left: 17px !important;
}
.VisOptionViewDiv .optionsDiv .ant-select-selector .ant-select-selection-item {
  font-family: "NotoSansHans-Regular";
  font-size: 14px;
  color: #6D757A;
  height: 20px;
  line-height: 20px;
}
.VisOptionViewDiv .optionsDiv .ant-select-selection--single .ant-select-selection__rendered {
  height: 20px;
  margin-left: 17px !important;
}
.VisOptionViewDiv .optionsDiv .ant-select-selection-selected-value {
  font-family: "NotoSansHans-Regular";
  font-size: 14px;
  color: #6D757A;
  height: 20px;
  line-height: 20px;
}
